import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGlobeEurope } from "@fortawesome/free-solid-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import "./style.css"

// Props are passed through our functional component.
function ProjectCard(props) {
  const coverImage = props.project.coverPhoto;

  const onClick_handler = () => {
    window.open(props.project.url_deployed)
  };

  const onClick_handler_git = () => {
    window.open(props.project.url_gitHub)
  };

  return (
    <div
      className="card box-border h-35 w-31 border-4 m-5 flex flex-wrap flex-col"
      key={props.project.id}
    >
      <div className="text-gray-500 text-2xl font-bold p-3 ">
        {props.project.name}
      </div>

      <div className="img-container OnHover">
        <img
          key={props.project.id}
          alt={props.project.coverPhoto}
          src={`${props.project.coverPhoto}`}
          width="300"
          onClick={onClick_handler}
        />
        {console.log(props.project.coverPhoto)}
      </div>
      <div className="content flex flex-col text-gray-500 text-sm ml-2">
        <div  onClick={onClick_handler}>
          <FontAwesomeIcon icon={faGlobeEurope} /> <span className="text-xs OnHover">{props.project.url_deployed}</span>
        </div>
        <div onClick={onClick_handler_git}>
          <FontAwesomeIcon icon={faGithub} />  <span className="text-xs OnHover">{props.project.url_gitHub}</span>
        </div>
      </div>
    </div>
  );
}

export default ProjectCard;
